import React, { Component } from 'react';
import { UserPreview } from '../cmps/UserPreview';
import { contactService } from '../services/contact.service';
import { bitcoinService } from '../services/bitcoin.service';

export class Home extends Component {
  state = {
    loggedInUser: null,
    rate: null,
  };

  componentDidMount() {
    this.setLoggedInUser();
    this.loadRate();
  }

  setLoggedInUser = async () => {
    try {
      const loggedInUser = await contactService.getLoggedInUser();
      this.setState({ loggedInUser });
    } catch (err) {
      console.log('err:', err);
    }
  };

  loadRate = async () => {
    try {
      const rate = await bitcoinService.getRate();
      this.setState({ rate });
    } catch (err) {
      console.log('had a big poopoo :', err);
    }
  };

  render() {
    const { loggedInUser, rate } = this.state;
    return (
      <div>
        {loggedInUser && <UserPreview user={loggedInUser} rate={rate} />}
      </div>
    );
  }
}
